import React from "react"
import {graphql, Link} from "gatsby"
import Layout from "../Components/Layout";
import Img from 'gatsby-image'
import {SEO} from "theme-seo";

const Blog = ({data, pageContext}) => {
    const sortedPosts = data.allMarkdownRemark.nodes
    return(
        <Layout>
            <SEO title={pageContext.title}/>
            <div className="w-10/12 lg:w-4/5 mx-auto py-1">
                {sortedPosts.map(node => (
                    <Link to={`/blog${node.fields.slug}`} className="flex flex-col lg:block my-10 p-6 w-full text-right hover:bg-gray-100 shadow-md" data-aos="fade-up" key={node.id}>
                        <div className="lg:flex justify-start">
                                {data.allFile.nodes.filter(n => n.name === node.frontmatter.icon).length > 0 &&
                                <img className="my-4 mx-auto lg:m-0 bg-white lg:ml-6  shadow-md rounded" width="150" height="150" src={data.allFile.nodes.filter(n => n.name === node.frontmatter.icon)[0].publicURL}/>
                                }
                            <div>
                                <div className="text-xl"><p className="pt-1 pb-8 text-gray-700 text-center lg:text-right text-xl">{node.frontmatter.title}</p></div>
                                <div className=" text-gray-700 text-base" dangerouslySetInnerHTML={{__html: node.frontmatter.excerpt ? node.frontmatter.excerpt : node.excerpt }}/>
                            </div>
                            <div className="lg:hidden flex justify-end clear-both ">
                                <div className="m-2 w-1/3 border-2 bg-gray-200  hover:bg-gray-300 text-gray-600 text-center font-bold py-2 px-4 rounded-full rounded-bl-none lg:w-1/6 text-gray-300 " >ادامه</div>
                            </div>
                        </div>
                    </Link>
                ))}
            </div>

        </Layout>
    );
}

export default Blog;

export const query = graphql`
{
   allMarkdownRemark(
        filter: {fileAbsolutePath: {regex: "/post/"  }}
        sort: { order: DESC, fields: [frontmatter___date] }
          limit: 100
        ) {
          nodes {
            id
            html
            excerpt(pruneLength: 200, format: PLAIN, truncate: true)
            frontmatter{
             date(formatString: "MMMM DD, YYYY")
             title
             icon
             excerpt
             }
            fields {
              slug
            }
          }
        }
        
        allFile(filter: { sourceInstanceName: { eq: "post" }, extension:{ne:"md"}}) {
        nodes {
          name 
          publicURL
          extension
        }
  }
}
`